/**
 * 释放答题权限，发生场景在主播老师说GO后执行此api，此时学生进入答题倒数状态，对应打点文件的start.test
 * @param {number} tid 
 * 打点文件的tid
 * @returns {object} 
 * {"type":"read","read_type":"word","read_content":"hello"}
 */
import { data } from '../globalVar';
import { showMask } from '../../utils/showMask';
import * as EnableAnswerAction from '../EnableAnswerAction';
import { game } from '../../state/game';
import { message } from '../../utils/message';

const courseware = data && data['courseware'];


function enableAnswer(tid) {
    message('---->允许答题tid:' + tid);

    let item = courseware[tid - 1];
    let second = item['data']['timeout'] ? item['data']['timeout'] : 9;
    showMask(false);

    let typeArr = ['read', 'choice', 'multiselect', 'survey', 'jigsaw', 'cation'];
    if (typeArr.indexOf(item['type']) > -1) {
        return EnableAnswerAction[item['type']](game, item, second);
    } else {
        return {
            type: item['type']
        };
    }

    return tid;

}

export {
    enableAnswer
}


