


/**
 * 监听用户跟读音量
 * @param {number} ;///注 value-->音量大小数值类型
 */
import { data } from '../globalVar';
import { starExplosion } from '../../animations/starExplosion';
import { game } from '../../state/game';

let getVolume = function (tid, value) {

    let item = data.courseware[tid - 1];
    value = parseInt(value, 10);
    //loadTexture
    let speed = value;


    /**
     * 音量大小条
     */
    let volumeView = parseInt(value * 2.7 / 10, 10);

    for (let i = 0; i < 10; i++) {
        if (i < volumeView) {
            item['volumeArr'][i]['alpha'] = 1;
        } else {
            item['volumeArr'][i]['alpha'] = 0;
        }
    }

    if (volumeView > 8) {
        if (item['starFlag']) {
            item['starFlag'] = false;

            item['read_star_big'].loadTexture('read_star_big', 0);

        }
        if (item['maxVolume'] && !item['maxVolume'].isPlaying) {
            // item['maxVolume'].play();
        }

        starExplosion(game, 'read_star_small', [0], { x: 705 + 64 / 2, y: 454 + 64 / 2 });
    }

    // if (value < 25) {
    // item['eyes'].loadTexture('read_eyes', 0);
    // item['mouthHightAni'].stop();

    // isPlaying

    // if (!item['mouthLowAni'].isPlaying) {
    //     item['mouthLowAni'].play(speed, true);
    // } else {
    //     item['mouthLowAni'].speed = speed;
    // }



    // } else {
    // item['eyes'].loadTexture('read_eyes', 1);
    // item['mouthLowAni'].stop();
    // speed = speed / 2;
    // if (speed > 25) {
    //     speed = 25;
    // }

    // if (!item['mouthHightAni'].isPlaying) {

    //     item['mouthHightAni'].play(speed, true);
    // } else {
    //     item['mouthHightAni'].speed = speed;

    // }

    // }






}

function h() {
    console.log('是阿罗汉发');
}

export {
    getVolume
}


