
import { scoreToStar } from '../../utils/scoreToStar';
import { uploadScore } from '../callNativeFunction/uploadScore';
import { getScoreStar } from '../../animations/getScoreStar';
import { starCollected } from '../../animations/starCollected';
import { data } from '../globalVar';
import { game } from '../../state/game';
const courseware = data && data['courseware'];

/**
 * 返回语音评测分数
 * @param {object} 
 * {"code":"success","score":88.88,"tid":1}
 */

function stopRecord(obj) {
    obj = JSON.parse(obj);

    let item = courseware[obj['tid'] - 1];

    let score = scoreToStar(obj['score'])

    uploadScore({ "num": score, "tid": obj['tid'], action: 1 });

    for (let i = 0; i < item['volumeArr'].length; i++) {
        item['volumeArr'][i]['alpha'] = 0;
    }
    if (score > 0) {
        getScoreStar(score);
        window['GAME_TOTAL_STAR'] += score;
        starCollected(window['GAME_TOTAL_STAR']);

        item['mouth'].loadTexture('read_mouth', 5);//好
        item['eyes'].loadTexture('read_eyes', 2);//好

        game.sound.stopAll();
        let audio = null;
        if (score === 1) {

            audio = game.add.audio('Sound_Great');


        } else if (score === 2) {
            let temArr = ['Sound_Wonderful', 'Sound_Awesome', 'Sound_Super', 'Sound_Wow_stupendous'];
            let temIndex = Math.floor(Math.random() * 4);

            audio = game.add.audio(temArr[temIndex]);
        } else if (score === 3) {
            let temArr = ['Sound_Amazing', 'Sound_Fantastic'];
            let temIndex = Math.floor(Math.random() * 2);

            audio = game.add.audio(temArr[temIndex]);

        }

        audio && audio.play();
    } else {
        // let GameOverAudio = game.add.audio('GameOverAudio');
        // game.sound.stopAll();
        // GameOverAudio.play();
        item['mouth'].loadTexture('read_mouth', 6);//不好
        item['eyes'].loadTexture('read_eyes', 3);//不好
    }

    return obj['tid'];


}

export {
    stopRecord
}


