import { game } from './state/game';
import * as bootStates from './state/index';
import * as topicStates from './topic/index';
import { goToState } from './state/stateManager';

require('./css/index.scss');

/**
 * state入口
 */
for (let boot in bootStates) {
    game.state.add(boot, bootStates[boot]);
}
/**
 * topic state
 */
for (let topic in topicStates) {
    game.state.add(topic, topicStates[topic]);
}


game.state.start('boot');

window.game = game;
window.goToState = goToState;


