

function showLoading(isShow, errFlag) {

    let loading = window.document.getElementById('abc_time_loading');

    if (isShow) {
        loading.style.display = 'block';
    } else {
        loading.style.display = 'none';
    }
    if (errFlag) {
        loading.className = "error_loading";
    }




}

export { showLoading };


