
// import { message } from '../utils/message';
import { data } from '../base/globalVar';

let timerClock = function (second, game, callback) {

    let px = 600;
    if (data && data['page_size']) {
        let size = data['page_size'];
        if (size.w === 1024) {
            px -= 128;
        }

    }

    let clock = game.add.sprite(px, 16, 'choice_clock');

    let graphics = game.add.graphics(0, 0);

    //  Our first arc will be a line only
    graphics.lineStyle(4, 0xFF9500);

    let ratio = 36 / second;
    let startDeg = -90;
    let deg = startDeg;

    let img = game.add.sprite(px + 40, 56, 'choice_clock_pointer');
    img.scale.setTo(-1, 1);
    img.anchor.setTo(1, 0.5);
    img.angle = startDeg;
    // let i = 0;

    let drawCircl = setInterval(() => {
        // console.log('jj')
        // i += 1;
        // message(`<div style="color:green">${i}</div>`)

        deg += ratio;
        if (deg >= 270) {
            clearInterval(drawCircl);
            callback && callback();
            graphics.kill();
            img.kill();
            clock.kill();
        }
        img.angle = deg;
        graphics.arc(px + 40, 56, 30, game.math.degToRad(startDeg), game.math.degToRad(deg), false);

    }, 100);
    window['global_temp_interval_timer'].push(drawCircl);


}

export {
    timerClock
};




