
import { dsBridge } from '../globalVar';
import { message } from '../../utils/message';

const envValue = window.location.href.split('?')[1];


/**
 * 给原生上报获得星星
 * @param {object} obj 
 * 上传得星数 {"num":3,"tid":1} num:星星数 tid:题号
 */
function uploadScore(obj) {
    //同步
    message('给原生上报获得星星--->tid:' + obj['tid']);

    console.log(obj);
    console.log('给原生上报获得星星');

    if (envValue === 'debug') {
        console.warn('给原生上报获得星星----->  开发环境不调用原生');
    } else {
        dsBridge.call('echo.uploadScore', obj);
    }
}


export {
    uploadScore
}


