import { Phaser } from '../base/globalVar';
import { data } from '../base/globalVar';

let size;

if (data && data['page_size']) {
    size = data['page_size'];

    if (size.w === 1024) {
        let dom = document.getElementById('abc_star');
        dom.style.left = '2.2rem';
    }
} else {
    size = {
        w: 1280,
        h: 720
    };
}



const game = new Phaser.Game(size.w, size.h, Phaser.CANVAS, 'root');
game.transparent = true;
export {
    game
};


