
function starCollected(total_star) {

    let abc_star = document.getElementById('abc_star');
    // let span = document.querySelector('#abc_star span');
    abc_star.className = 'abc_star_active';
    // span.innerHTML = starNum;

    setTimeout(() => {
        abc_star.className = '';

    }, 300)
    let span = document.querySelector('#abc_star span');
    span.innerHTML = total_star;


}


/**
 * 数组洗牌
 * @param {*} arr
 */
function shuffle(arr) {
    var len = arr.length;
    for (var i = 0; i < len - 1; i++) {
        var idx = Math.floor(Math.random() * (len - i));
        var temp = arr[idx];
        arr[idx] = arr[len - i - 1];
        arr[len - i - 1] = temp;
    }
    return arr;
}

// [{_id: "5ab898001902ed3faaaf657a", lesson_id: 1234, uid: 124, qnum: 2, group: "0",avatar:"http://"}];
function topThree(arr) {
    let str = '';

    arr = JSON.parse(arr);
    let len = arr.length;

    let defaultArr = [require('../public-image/topThree/1.png'), require('../public-image/topThree/2.png'), require('../public-image/topThree/3.png')];

    defaultArr = shuffle(defaultArr);

    if (len === 0) {
        arr.push({ avatar: defaultArr[0] });
        arr.push({ avatar: defaultArr[1] });
        arr.push({ avatar: defaultArr[2] });
    } else if (len === 1) {
        arr.push({ avatar: defaultArr[0] });
        arr.push({ avatar: defaultArr[1] });
    } else if (len === 2) {
        arr.push({ avatar: defaultArr[0] });
    }

    arr.map((item, index) => {
        let temp = ' <div><img src="' + item['avatar'] + '" alt=""></div>';
        str += temp;
        return item;

    });
    let box = document.querySelector('#abc_top_three');
    console.log(box);
    box.innerHTML = str;

    let abc_top_three = document.querySelectorAll('#abc_top_three div');

    if (abc_top_three) {

        abc_top_three.forEach((item, index) => {
            item.className = 'abc_top_three_active';

            if (index === 0) {
                setTimeout(() => {
                    item.className = 'abc_top_three_active_right';

                }, 500);
            } else if (index === 1) {
                setTimeout(() => {
                    item.className = 'abc_top_three_active_right2';

                }, 1000);
            } else if (index === 2) {
                setTimeout(() => {
                    item.className = 'abc_top_three_active_right3';

                }, 1150);
            }


        });
    }




}

window.topThree = topThree;


export {
    starCollected,
    topThree
};


