/**
 * set目前总星星数
 * @param {number} total_star
 */
import { starCollected } from '../../animations/starCollected';
let setTotalStar = function (total_star) {
    window['GAME_TOTAL_STAR'] = total_star;
    starCollected(total_star);
    return total_star;
}

export {
    setTotalStar
};


