import { data } from '../globalVar';
import {game} from "../../state/game";
// import { game } from '../../state/game';
// import {message} from "../../utils/message";

const courseware = data && data['courseware'];

function getsurveyData (json) {
    // message(JSON.stringify(json));
    // var json = json || {tid: 4, rank: [1, 1, 9], person_num: 11};
    let item = courseware[json.tid - 1];
    // item.bmdArr = [];

    if (item['type'] === 'survey') {
        for (let i = 0; i < item['data']['body'].length; i++) {
            var ii = json['rank'][i];
            if ((120 / (json['person_num'] || 1) * ii + 40 <= 120) && ii > 0) {
                let xPosition = item['data']['body'][i]['pos']['x'] + (item['data']['body'][i]['size']['w'] - 160) / 2;
                let yPosition = item['data']['body'][i]['pos']['y'] + item['data']['body'][i]['size']['h'] - 50;
                let bmd = game.make.bitmapData(40 + ii * (120 / json['person_num']), 50);
                // item.bmdArr.push(bmd);
                bmd.alphaMask('survey_bar2','survey_bar2')
                bmd.myz = 11;
                var bmdDel = game.add.sprite(xPosition, yPosition, bmd);
                item.progressRed.push(bmdDel)
                // bmdDel.kill()
                let text = game.add.text(xPosition + bmd.width - 30, yPosition, 'ff', { fill: 'white', fontSize: 20 });
                item.progressText.push(text);
                text.myz = 12;
                text.text = parseInt(ii, 10);
            }
        }
    }
}

export {
    getsurveyData
}


