import { data } from '../globalVar';
import { showMask } from '../../utils/showMask';
import * as DisableAnswerAction from '../DisableAnswerAction';
import { game } from '../../state/game';
import { message } from '../../utils/message';

const courseware = data && data['courseware'];
/**
 * 关闭答题权限，发生场景在打点文件的stop.test事件
 */

function disableAnswer(tid) {
    showMask(true);
    let item = courseware[tid - 1];
    message('--->不允许答题tid:' + tid);

    if (DisableAnswerAction[item['type']]) {
        DisableAnswerAction[item['type']](game, item, tid);
    } else {
        console.log('disableAnswer---default action');
    }


    return tid;
}

export {
    disableAnswer
}



