import { game } from './game';
import { data } from '../base/globalVar';
import { message } from '../utils/message';
import { removeAll3dBox } from '../animations/readCss3d';

/**
 * 
 * @param {number} index 索引
 */
function goToState(index) {
    let nowDate = new Date();

    let courseware = data ? data['courseware'] : [];
    window.addEventListener('error', function (msg, url, line) {
        let str = 'msg:' + msg.message + ' url:' + url + ' line:' + line;

        message('<div style="color:red">' + str + '</div>');
    })
    if (courseware[index]) {
        message("第" + (index + 1) + "题:" + courseware[index]['type'] + "--->" + nowDate.getHours() + "时 " + nowDate.getMinutes() + "分 " + nowDate.getSeconds() + "秒 ");

    } else {
        message("第" + (index + 1) + "题:不存在");
    }



    if (courseware.length > 0) {
        if (courseware[index]) {
            removeAll3dBox();
            for (let i = 0; i < window['global_temp_interval_timer'].length; i++) {
                clearInterval(window['global_temp_interval_timer'][i]);
            }


            for (let i = 0; i < window['global_temp_settimeout_timer'].length; i++) {
                clearTimeout(window['global_temp_settimeout_timer'][i]);
            }
            window['global_temp_interval_timer'] = [];
            window['global_temp_settimeout_timer'] = [];
            cancleTouchEvent(game);

            game.state.start(courseware[index]['type'], true, false, courseware[index]);
        } else {

            console.error("index:" + index + '没有该index课件资源！');
        }
    } else {
        console.error('data ---- courseware --- 没有数据');
    }
}

/**
 * 清楚游戏对象触摸事件
 * @param {object} game 游戏对象
 */
function cancleTouchEvent(game) {
    game.input.touch.touchStartCallback = null;
    game.input.touch.touchMoveCallback = null;
    game.input.touch.touchEndCallback = null;
}

export {
    goToState
};


