
/**
 * 爆炸:星星效果
 */
import { data } from '../base/globalVar';


let starExplosion = function (game, pic, arr, poz) {
    let emitter;
    if (!poz) {



        emitter = game.add.emitter(game.world.centerX, game.world.centerY, 200);
    } else {
        let emitterX = poz.x;

        if (data && data['page_size']) {
            let size = data['page_size'];
            if (size.w === 1024) {
                emitterX -= 128;
            }

        }
        emitter = game.add.emitter(emitterX, poz.y, 200);
    }
    // emitter.makeParticles('balls', [0, 1, 2, 3, 4, 5]);
    let speed = 500;
    emitter.makeParticles(pic, arr);
    emitter.minParticleSpeed.setTo(-speed, -speed);
    emitter.maxParticleSpeed.setTo(speed, speed);
    // emitter.minParticleScale = 0.5;
    // emitter.maxParticleScale = 2;
    emitter.gravity = 100;
    emitter.bounce.setTo(0.5, 0.5);
    emitter.angularDrag = 60;
    emitter.explode(300, 5);//一个点炸开 1颗粒存在时间 /2 砸开数量

}

export {
    starExplosion
};


