import { dsBridge } from '../globalVar';
// import {message} from "../../utils/message";
import { getVoteData } from "./getVoteData";

const envValue = window.location.href.split('?')[1];


/**
 * 给原生上报获得投票
 * 上传投票数据
 * @param {object} obj 
 * {"item":"a","tid":1} item:选项  tid
 */
function putVoteData(obj, i) {
    //同步
    var temp = {};
    var letter = ['a', 'b', 'c', 'd', 'e', 'f', 'g'];
    temp.item = letter[i];
    temp.tid = obj['id'];
    temp.action = 1;
    // temp = JSON.parse(temp);
    if (envValue === 'debug') {
        console.warn('投票题----->  开发环境不调用原生');
    } else {
        dsBridge.call('echo.putVoteData', temp, function (data) {
            getVoteData(obj)
        });
    }

}


export {
    putVoteData
}


