
let playvoice = function (game, success) {
    /**
           * 音效
           */
    if (success) {

        let SoundtemArr = ['Sound_Amazing', 'Sound_Fantastic'];
        let SoundtemIndex = Math.floor(Math.random() * 2);

        let SoundAudio = game.add.audio(SoundtemArr[SoundtemIndex]);
        SoundAudio && SoundAudio.play();
    } else {

        let SoundAudio = game.add.audio('Sound_Wrong2');
        SoundAudio && SoundAudio.play();
    }
}


export {
    playvoice
};


