
import { Phaser, game } from '../globalVar';
import { timerClock } from '../../animations/timerClock';
// import { disableAnswer } from '../registerFunction/disableAnswer';
import { hideLock } from '../../animations/hideLock';

let multiselect = function (game, item, second) {

    hideLock(game, item['lockGroup']);
    hideLock(game, item['maskGroup']);
    if (item['optionArr']) {
        item['optionArr'].map((op) => {
            let tw = game.add.tween(op);

            tw.to({
                alpha: 1
            }, 300, Phaser.Easing.Linear.None, true);
        })

    }


    timerClock(second, game, () => {
        console.log('jisu')

        // disableAnswer(item['id'])
    });



    return {
        type: item['type']
    };
}

export {
    multiselect
}


