import { game } from '../state/game';
// import { uploadScore } from '../base/callNative';
import { showMask } from '../utils/showMask';
import { Phaser } from '../base/globalVar';
// import { rect } from '../utils/rect';
import { drawText } from '../utils/drawText';

const jigsaw = {
    init(data) {
        this.item = data;


    },
    preload() {

    },
    create() {
        showMask(true);
        let item = this.item;
        item['answerIsTrue'] = false;
        item['isAnswered'] = false;

        drawText(game, item.data.text, 660);

        this.group = game.add.group();

        /**
         * groupA
         */
        this.groupA = game.add.group();
        this.groupB = game.add.group();
        this.groupA.myz = 9;
        this.groupB.myz = 9;
        this.group.add(this.groupA);
        this.group.add(this.groupB);

        item['groupASprite'] = this.groupA;
        item['groupBSprite'] = this.groupB;

        let answerCount = 0;

        if (item && item['data'] && item['data']['groupA']) {
            let body = item['data']['groupA'];


            for (let i = 0; i < body.length; i++) {
                let temA = game.add.sprite(body[i]['pos']['x'], body[i]['pos']['y'], item['id'] + 'groupA' + i);
                temA.temName = body[i]['answer'];
                this.groupA.add(temA);
                // rect(game, body[i]['pos']['x'], body[i]['pos']['y'], temA, 0xffff00);
            }
        }

        /**
         * groupB
         */
        if (item && item['data'] && item['data']['groupB']) {
            let body = item['data']['groupB'];

            for (let i = 0; i < body.length; i++) {
                let temB = game.add.sprite(body[i]['pos']['x'], body[i]['pos']['y'], item['id'] + 'groupB' + i);
                // rect(game, body[i]['pos']['x'], body[i]['pos']['y'], temB, 0xff0000)
                temB.temName = i;
                temB.inputEnabled = true;
                temB.input.enableDrag();
                temB.originPosition = {
                    x: body[i]['pos']['x'],
                    y: body[i]['pos']['y']
                };


                temB.events.onDragStop.add((a, b) => {
                    item['isAnswered'] = true;
                    game.add.tween(a).to({
                        x: a['originPosition']['x'],
                        y: a['originPosition']['y'],
                    }, 300, Phaser.Easing.Linear.None, true, 0, 0, false);




                    game.physics.arcade.overlap(temB, this.groupA, (player, enemy) => {


                        if (temB['temName'] + '' === enemy['temName'] + '') {
                            console.log('成功')
                            answerCount += 1;
                            if (answerCount === item['data']['groupA'].length) {
                                console.log('全部成功！！！');
                                item['answerIsTrue'] = true;
                            }
                            setTimeout(() => {
                                game.add.tween(temB).to({
                                    x: enemy['position']['x'],
                                    y: enemy['position']['y'],
                                }, 300, Phaser.Easing.Linear.None, true, 0, 0, false);
                                temB.inputEnabled = false;
                            });

                        }

                    }, (jjj) => {


                    }, this);





                }, this)
                this.groupB.add(temB);
            }
        }

        game.physics.arcade.enable(this.groupB);

        game.physics.arcade.enable(this.groupA);




        /**
         * title标题
         */


        if (item && item['data'] && item['data']['title']) {
            let title = item['data']['title'];

            for (let i = 0; i < title.length; i++) {
                let img = game.add.sprite(title[i]['pos']['x'], title[i]['pos']['y'], item['id'] + 'title' + i);

                this.title = img;

                img.myz = 100;
                this.group.add(img);

            }

        }

        /**
         * 其他图片other_images
         */
        if (item && item['data'] && item['data']['other_images']) {
            let other_images = item['data']['other_images'];

            for (let i = 0; i < other_images.length; i++) {
                let img = game.add.sprite(other_images[i]['pos']['x'], other_images[i]['pos']['y'], item['id'] + 'other_images' + i);
                img.myz = 8;
                this.group.add(img);
            }
        }


        /**
         * 图片body
         */
        if (item && item['data'] && item['data']['body']) {
            let body = item['data']['body'];

            for (let i = 0; i < body.length; i++) {
                let img = game.add.sprite(body[i]['pos']['x'], body[i]['pos']['y'], item['id'] + 'body' + i);
                img.myz = 8;
                this.group.add(img);
            }
        }




    },

    render() {
        this.group.sort('myz', Phaser.Group.SORT_ASCENDING);
    }
};


export { jigsaw };


