function getScoreStar(score) {
    let abc_star = document.querySelector('.getScoreBox');
    // let span = document.querySelector('#abc_star span');
    abc_star.className = 'getScoreBox getScoreBox_active';
    // span.innerHTML = starNum;

    setTimeout(() => {
        abc_star.className = 'getScoreBox';

    }, 4500);

    let spanArr = document.querySelectorAll('.getScoreBox .item');
    for (let i = 0; i < spanArr.length; i++) {
        if (i + 1 !== score) {
            spanArr[i].className = 'item';
        } else {
            spanArr[i].className = 'item item_active';

        }
    }


}



window.getScoreStar = getScoreStar;

export { getScoreStar };


