

import { dsBridge } from '../globalVar';

const envValue = window.location.href.split('?')[1];
/**
 * 课件图片等资源加载完毕调用----用于告诉原生课件资源已经加载完成了。
 * @param {object} obj 课件加载完毕回调事件 
 * {"courseData":"[]"}
 */
function courseLoaded(obj) {
    //同步
    console.log(obj);
    console.log(JSON.stringify({"type": "loaded"}))

    console.log('课件图片等资源加载完毕调用----用于告诉原生课件资源已经加载完成了。');

    if (envValue === 'debug') {
        console.warn('课件图片等资源加载完毕调用----用于告诉原生课件资源已经加载完成了。----->  开发环境不调用原生');
    } else {
        dsBridge.call('echo.courseLoaded', obj);

    }
}

export {
    courseLoaded
}


