
import { Phaser } from '../globalVar';
import { timerClock } from '../../animations/timerClock';
// import { disableAnswer } from '../registerFunction/disableAnswer';
import { getVoteData } from "../callNativeFunction";
import { message } from "../../utils/message";

let survey = function (game, item, second) {

    for (let i = 0; i < item['lockGroup']['children'].length; i++) {
        let tw = game.add.tween(item['lockGroup']['children'][i]);

        tw.to({
            alpha: 0
        }, 300, Phaser.Easing.Linear.None, true);
    }


    for (let i = 0; i < item['maskGroup']['children'].length; i++) {

        let tw = game.add.tween(item['maskGroup']['children'][i]);

        tw.to({
            alpha: 0
        }, 300, Phaser.Easing.Linear.None, true);
    }

    if (item['optionArr']) {
        item['optionArr'].map((op) => {
            let tw = game.add.tween(op);

            tw.to({
                alpha: 1
            }, 300, Phaser.Easing.Linear.None, true);
        })

    }


    timerClock(second, game, () => {
        console.log('jisu')

        // disableAnswer(item['id'])
    });

    var timingTimes = 0;
    let timingVote = window.setInterval(function () {
        getVoteData(item);
        timingTimes++;
        if (timingTimes === 5) {
            window.clearInterval(timingVote)
        }
    }, 2000);

    return {
        type: item['type']
    };
}

export {
    survey
}


