import { uploadScore } from '../callNativeFunction/uploadScore';
import { starCollected } from '../../animations/starCollected';
import { optionScale } from '../../animations/optionScale';
import { Phaser } from '../globalVar';
import { getScoreStar } from '../../animations/getScoreStar';
import { rect } from '../../utils/rect';
import { message } from '../../utils/message';
import { hideLock } from '../../animations/hideLock';
import { game } from '../globalVar';
import { playvoice } from './playvoiceA';

let choice = function (game, item, tid) {

    hideLock(game, item['lockGroup']);
    hideLock(game, item['maskGroup']);
    setTimeout(() => {
        if (item['backgroundGroup']) {
            hideLock(game, item['backgroundGroup'], 100);
        }

    }, 2000);


    if (item['optionArr']) {

        for (let i = 0; i < item['optionArr'].length; i++) {
            item['optionArr'][i]['myz'] = 9;
            let temTw = game.add.tween(item['optionArr'][i]);
            item['optionArr'][i].scale.setTo(1);
            temTw.to({
                alpha: 1
            }, 300, Phaser.Easing.Linear.None, true);

            optionScale(game, item['optionArr'][i], 300, 0.2);
        }
    }

    if (item['userChoosedBorder']) {
        item['userChoosedBorder'].kill();
    }


    if (item['userAnswer'] !== item['data']['answer']) {
        if (!item['isAnswered']) {
            console.log('维达')
            // let GameOverAudio = game.add.audio('GameOverAudio');
            // game.sound.stopAll();
            // GameOverAudio.play();
            if (item['data'].hasOwnProperty('playVoiceArr') && item['data']['playVoiceArr'].length) {
                let voice1 = game.add.audio(item['id'] + 'playVoiceArr0');
                voice1.play();
                console.log(111, item);
                item.overAnswerimggroup[0].alpha = 1;
                let nA = game.add.tween(item.overAnswerimggroup[0].scale);
                nA.to({ x: 2, y: 2 }, 500, Phaser.Easing.Linear.None, true);
                window.setTimeout(function () {
                    nA.to({ x: 0, y: 0 }, 500, Phaser.Easing.Linear.None, true);
                }, 1000)
            } else {
                playvoice(game, false);
            }
            uploadScore({ "num": 0, "tid": tid, action: 0 });

            message('单选题目没做答题')
        } else {
            uploadScore({ "num": 0, "tid": tid, action: 1 });

            console.log('已经答')
            message('单选题目已经答题目')
            if (item['data'].hasOwnProperty('playVoiceArr') && item['data']['playVoiceArr'].length) {
                let voice2 = game.add.audio(item['id'] + 'playVoiceArr2');
                voice2.play();
                message('单选题目已经答题目');
                item.overAnswerimggroup[2].alpha = 1;
                let cW = game.add.tween(item.overAnswerimggroup[2].scale);
                cW.to({ x: 2, y: 2 }, 500, Phaser.Easing.Linear.None, true);
                window.setTimeout(function () {
                    cW.to({ x: 0, y: 0 }, 500, Phaser.Easing.Linear.None, true);
                }, 1000)
            } else {
                playvoice(game, false);
            }
        }

        //选错
        let userItem = item['optionArr'][item['userAnswer']];
        if (userItem) {

            userItem.scale.setTo(1);
            let choice_error_tab = rect(game, userItem.x - userItem['width'] / 2, userItem.y - userItem['height'] / 2, userItem, 0xFF5320);
            choice_error_tab.myz = 12;
            item.stateGroup.add(choice_error_tab)

            optionScale(game, choice_error_tab, 300, 0.2, item['optionArr'], 2);
        }

        let answenItem = item['optionArr'][item['data']['answer']];
        if (answenItem) {
            let tick = game.add.image(answenItem.x, answenItem.y, 'choice_tick');
            tick.anchor.setTo(0.5);

            optionScale(game, tick, 300, 0.2, item['optionArr'], 2);
        }

    } else {
        //选对
        if (item['data'].hasOwnProperty('playVoiceArr') && item['data']['playVoiceArr'].length) {
            let voice3 = game.add.audio(item['id'] + 'playVoiceArr1');
            voice3.play();
            item.overAnswerimggroup[1].alpha = 1;
            let cR = game.add.tween(item.overAnswerimggroup[1].scale);
            cR.to({ x: 2, y: 2 }, 500, Phaser.Easing.Linear.None, true);
            window.setTimeout(function () {
                cR.to({ x: 0, y: 0 }, 500, Phaser.Easing.Linear.None, true);
            }, 1000)
        } else {
            /**
             *  音效
             */

            playvoice(game, true);
        }
        uploadScore({ "num": 3, "tid": tid, action: 1 });



        message('单选选对')
        window['GAME_TOTAL_STAR'] += 3;
        starCollected(window['GAME_TOTAL_STAR']);
        getScoreStar(3);
        let answenItem = item['optionArr'][item['data']['answer']];
        if (answenItem) {
            answenItem.scale.setTo(1);

            let tick = rect(game, answenItem.x - answenItem['width'] / 2, answenItem.y - answenItem['height'] / 2, answenItem, 0x3CCC79);
            tick.anchor.setTo(0.5);
            tick.myz = 12;
            item.stateGroup.add(tick);

            optionScale(game, tick, 300, 0.2, item['optionArr'], 2);
            let tickB = tick = game.add.image(answenItem.x, answenItem.y, 'choice_tick');
            tickB.anchor.setTo(0.5);
            optionScale(game, tickB, 300, 0.2, item['optionArr'], 2);

        }
    }



}

export {
    choice
}



