import { Phaser } from '../base/globalVar';

let hideLock = function (game, lockGroup, second) {
    second = second ? second : 300;
    if (lockGroup) {
        for (let i = 0; i < lockGroup['children'].length; i++) {
            let tw = game.add.tween(lockGroup['children'][i]);

            tw.to({
                alpha: 0
            }, second, Phaser.Easing.Linear.None, true);
        }
    }
}

export {
    hideLock
};


