
/**
 * 展示上榜的三人
 * @param {array} arr  
 * [{_id: "5ab898001902ed3faaaf657a", lesson_id: 1234, uid: 124, qnum: 2, group: "0",avatar:"http://"}];
 */
import { topThree } from '../../animations/starCollected';
import { message } from '../../utils/message';

function showTopThree(arr) {
    console.log(arr);
    topThree(arr);

    let nowDate = new Date();

    let str = typeof arr === 'object' ? JSON.stringify(arr) : arr;
    str +="--->" + nowDate.getHours() + "时 " + nowDate.getMinutes() + "分 " + nowDate.getSeconds() + "秒 "

    message("<div style='color:orange'>" + str + "</div>");

    return true;
}

export {
    showTopThree
};


