
// import { Phaser } from '../base/globalVar';

let rect = function (game, x, y, img, color, isMask) {

    let graphics = game.add.graphics(x, y);

    graphics.lineWidth = 10;
    if (isMask) {
        graphics.lineWidth = 0;
        graphics.lineColor = 0xFFFFFF;
    } else {
        graphics.lineColor = color;
    }
    graphics.width = img.width;
    graphics.height = img.height;
    if (isMask) {
        graphics.beginFill(0xFFFFFF);
        graphics.drawRoundedRect(0, 0, img.width - 10, img.height - 10, 60);
        graphics.endFill();
        graphics.alpha = 0.7;
    } else {
        graphics.drawRoundedRect(0, 0, img.width - 10, img.height - 10, 40);
    }




    return graphics;


}

let rectBackground = function (game, x, y, w, h, color, r, alpha) {
    let graphics = game.add.graphics(x, y);

    graphics.lineWidth = 0;
    graphics.lineColor = color;

    graphics.width = w;
    graphics.height = h;

    graphics.beginFill(color);
    graphics.drawRoundedRect(0, 0, w, h, r);
    graphics.endFill();
    graphics.alpha = alpha;
    graphics.alpha = 0;

    return graphics;

}


export {
    rect,
    rectBackground
};


