import { game } from '../state/game';
// import { uploadScore } from '../base/callNative';
import { showMask } from '../utils/showMask';
import { Phaser } from '../base/globalVar';
import { drawText } from '../utils/drawText';

const raildraw = {
    init(data) {
        this.item = data;


    },
    preload() {

    },
    create() {

        showMask(true);
        game['tempNewdrawTimer'] = []

        let item = this.item;
        let bmd = new Phaser.BitmapData(game, 'newdraw_img', game.width, game.height);
        let myPaintBmd = new Phaser.BitmapData(game, 'newdraw_img', game.width, game.height);
        game.add.image(0, 0, bmd);
        game.add.image(0, 0, myPaintBmd);
        drawText(game, item.data.text, 660);


        this.group = game.add.group();

        /**
         * 
         */







        /**
        * 其他图片title
        */
        if (item && item['data'] && item['data']['title']) {
            let title = item['data']['title'];

            for (let i = 0; i < title.length; i++) {
                let img = game.add.sprite(title[i]['pos']['x'], title[i]['pos']['y'], item['id'] + 'title' + i);

                this.title = img;

                img.myz = 100;
                this.group.add(img);

            }

        }

        /**
         * 其他图片other_images
         */
        if (item && item['data'] && item['data']['other_images']) {
            let other_images = item['data']['other_images'];

            for (let i = 0; i < other_images.length; i++) {
                let img = game.add.sprite(other_images[i]['pos']['x'], other_images[i]['pos']['y'], item['id'] + 'other_images' + i);
                img.myz = 8;
                this.group.add(img);
            }
        }


        /**
         * 图片body
         */
        if (item && item['data'] && item['data']['body']) {
            let body = item['data']['body'];

            for (let i = 0; i < body.length; i++) {
                let img = game.add.sprite(body[i]['pos']['x'], body[i]['pos']['y'], item['id'] + 'body' + i);
                img.myz = 8;
                this.group.add(img);
            }
        }


        //存放用户写的字
        this.userDrawArr = [];


        drawTask(JSON.parse(JSON.stringify(item)), 0, bmd, 0.3, 10);

        /**
         * 用户写字画板
         */

        // paintWord(game, this.userDrawArr, item, bmd);


    },
    shutdown() {
        game.tempNewdrawTimer.map((item) => {
            clearTimeout(item);
            return null;
        });
        console.log(removeEmptyArr(this.userDrawArr));



    },
    render() {
        this.group.sort('myz', Phaser.Group.SORT_ASCENDING);
    }
};

function railLine(item, bmd, game) {

    let scale = 2;
    let px = 280;
    let py = 0;
    let color = '#fff';

    let stardardData = JSON.parse(JSON.stringify(item.data.Pdata));
    let breakFlag = false;

    let userNeedDraw = []
    for (let i = 0; i < stardardData.length; i++) {
        userNeedDraw.push('beginPoint');
        for (let j = 0; j < stardardData[i].length; j++) {

            userNeedDraw.push(stardardData[i][j])
        }



    }

    let len = userNeedDraw.length

    for (let i = 0; i < len; i++) {
        userNeedDraw.lastItem = userNeedDraw[len - 1];
        if (userNeedDraw[i] === 'beginPoint' && userNeedDraw[i + 1]) {
            let p = userNeedDraw[i + 1];

            item['frog'] = game.add.image(transformXY(p.x, px, scale), transformXY(p.y, py, scale), 'raildraw_frog');
            item['frog'].anchor.setTo(0.5);

            item['frog'].inputEnabled = true;
            item['frog'].input.enableDrag();
            let pen = mypen(color, bmd);



            // item['frog'].events.onDragStart.add(function () {

            // }, this);
            item['frog'].events.onDragUpdate.add(function (e) {
                // console.log(userNeedDraw[0]);
                if (!userNeedDraw[0]) {
                    item['frog'].inputEnabled = false;
                    let p = userNeedDraw['lastItem'];
                    item['frog'].position.x = transformXY(p.x, px, scale);
                    item['frog'].position.y = transformXY(p.y, py, scale);
                    item['frog'].alpha = 0;
                }
                if (userNeedDraw[0] === 'beginPoint') {
                    userNeedDraw.shift();
                    if (userNeedDraw[0]) {
                        let p = userNeedDraw[0];
                        pen.beginPath();
                        pen.moveTo(transformXY(p.x, px, scale), transformXY(p.y, py, scale));
                        item['frog'].position.x = transformXY(p.x, px, scale);
                        item['frog'].position.y = transformXY(p.y, py, scale);

                        userNeedDraw.shift();
                    }
                } else {
                    if (userNeedDraw[0]) {

                        let p = userNeedDraw[0];

                        let tx = transformXY(p.x, px, scale);
                        let ty = transformXY(p.y, py, scale);

                        pen.lineTo(tx, ty);
                        pen.stroke();
                        let x = item['frog'].x;
                        let y = item['frog'].y;


                        let val = Math.sqrt(Math.pow((x - tx), 2) + Math.pow((y - ty), 2));
                        if (val < 30) {
                            userNeedDraw.shift();
                            let newP1 = userNeedDraw[1];


                            if (newP1 && newP1 !== 'beginPoint') {

                                let newP1tx = transformXY(newP1.x, px, scale);
                                let newP1ty = transformXY(newP1.y, py, scale);
                                let sin = (newP1ty - ty) / Math.sqrt(Math.pow((newP1ty - ty), 2) + Math.pow((newP1tx - tx), 2));
                                let dx = newP1tx - tx;
                                let dy = newP1ty - ty;
                                let addDeg = 0;

                                let d = game.math.radToDeg(Math.asin(sin))

                                item['frog'].angle = d - 90;

                            }


                        }

                        if (val > 100) {
                            item['frog'].position.x = tx;
                            item['frog'].position.y = ty;
                        }




                    }

                }

            }, this);


            item['frog'].events.onDragStop.add(function (e) {
                if (userNeedDraw[0] && userNeedDraw[0] !== 'beginPoint') {

                    let p = userNeedDraw[0];

                    pen.lineTo(transformXY(p.x, px, scale), transformXY(p.y, py, scale));
                    pen.stroke();
                    item['frog'].position.x = transformXY(p.x, px, scale);
                    item['frog'].position.y = transformXY(p.y, py, scale);
                    // userNeedDraw.shift();

                }
            }, this);

            break;
        }
    }
}

/**
 * 去除空笔画
 * @param {Array} arr 笔画数组-
 */
function removeEmptyArr(arr) {
    for (var i = 0; i < arr.length; i++) {
        if (arr[i].length === 0) {
            arr.splice(i, 1);
            i = i - 1;
        }
    }
    return arr;
}

/**
 * 生成画板  用户比划轨迹
 * @param {object} game 游戏对象
 * @param {Array} arr 用户存放笔画的数组
 * @param {object} data 当前state的数据
 * @param {object} bmd BitmapData
 */
function paintWord(game, arr, data, bmd) {
    let that = {};
    that.flag = true;

    game.input.touch.touchStartCallback = function (e) {


        that.graphics = mypen('orange', bmd);

        that.flag = false;
        arr.push([]);


    };
    game.input.touch.touchMoveCallback = function (e) {
        console.log('mov')

        var temArr = arr[arr.length - 1];
        if (!that.flag) {
            that.flag = true;
            that.graphics.beginPath();
            that.graphics.moveTo(game.input.activePointer.x, game.input.activePointer.y)
        } else {
            that.graphics.lineTo(game.input.activePointer.x, game.input.activePointer.y)
            that.graphics.stroke();

        }
        temArr.push({
            x: game.input.activePointer.x,
            y: game.input.activePointer.y
        });

    }
    game.input.touch.touchEndCallback = function (e) {


    }
}



/**
 * 生成画笔上下文
 * @param {string} color 画笔颜色
 * @param {object} bmd BitmapData
 */
function mypen(color, bmd) {

    var ctx = bmd.ctx;

    ctx.lineCap = 'round';
    ctx.lineWidth = 60;
    ctx.strokeStyle = color;


    return ctx;
}

/**
 * 坐标变换
 * @param {number} x 原始坐标
 * @param {number} px 偏移量
 * @param {number} scale 放大缩小比例
 */
function transformXY(x, px, scale) {
    return (x + px) * scale;
}


/**
 * 一个笔画
 * @param {Array} arr 笔画数组---> 存放笔画点坐标
 * @param {object} graphics 画笔对象
 * @param {number} currentIndex  当前点
 * @param {number} time 画一点的时间
 * @param {function} callback 回调函数
 */
function drawLine(arr, graphics, currentIndex, time, callback) {
    let scale = 2;
    let px = 280;
    let py = 0;

    if (currentIndex <= arr.length - 1) {
        var timer = setTimeout(() => {

            let item = arr[currentIndex]

            if (item) {
                if (currentIndex === 0) {
                    graphics.beginPath();
                    graphics.moveTo(transformXY(item.x, px, scale), transformXY(item.y, py, scale));
                } else {
                    graphics.lineTo(transformXY(item.x, px, scale), transformXY(item.y, py, scale));
                    graphics.stroke();
                    if (currentIndex === arr.length - 1) {

                        callback && callback();
                    }

                }
            }

            drawLine(arr, graphics, currentIndex + 1, time, callback);

        }, time);

        game['tempNewdrawTimer'].push(timer);
    }

}

/**
 * 通过调用 drawLine 实现写一个字的功能
 * @param {Array} dataArr 笔画数组
 * @param {number} currentIndex 当前笔画
 * @param {object} bmd BitmapData
 * @param {number} time1 笔画停留时间
 * @param {number} time2 点停留时间
 */
function drawTask(item, currentIndex, bmd, time1, time2) {
    let dataArr = item.data.Pdata
    if (currentIndex <= dataArr.length) {


        if (dataArr && dataArr[currentIndex]) {
            drawLine(dataArr[currentIndex], mypen('#cd00cc78', bmd), 0, time2, function () {
                let mytime = setTimeout(function () {
                    console.log('swt')

                    drawTask(item, currentIndex + 1, bmd, time1, time2);
                }, time1 * 1000);

                game['tempNewdrawTimer'].push(mytime);

            });
        } else {
            railLine(item, bmd, game);
            console.log('还是理发了饭哈浪费哈撒')
        }
    }

}


export { raildraw };


