import { dsBridge } from '../globalVar';
import { getsurveyData } from "./getsurveyData";
// import {message} from "../../utils/message";

const envValue = window.location.href.split('?')[1];


/**
 * 获取投票数据
 * @param {object} obj 
 * {"items":"a,b,c","tid":1}  items:所有选项  tid:题号
 * @param {function} callback 
 * callback 参数即为返回值，参数 {"a":12,"b":11,"c":123} a,b,c选项投票结果
 */
function getVoteData(obj) {
    var temp = {};
    var letter = ['a', 'b', 'c', 'd', 'e', 'f', 'g'];
    temp.items = letter.slice(0, obj['data']['body'].length).join(',');
    temp.tid = obj['id'];
    // console.log(JSON.parse(temp));
    // var temp1 = JSON.parse(temp);
    //异步
    if (envValue === 'debug') {
        console.warn('获取投票数据----->  开发环境不调用原生');
    } else {
        dsBridge.call('echo.getVoteData', temp, function (data) {
            var data1 = JSON.parse(data);
            var json = {};
            json.tid = obj['id'];
            var arr = [];
            for (var i in data1['options']) {
                arr.push(data1['options'][i])
            }
            json.rank = arr;
            json.person_num = data1['person_num']
            getsurveyData(json);
        });
    }

}

export {
    getVoteData
}


