import { dsBridge } from '../globalVar';
import { enableAnswer } from './enableAnswer';
import { disableAnswer } from './disableAnswer';
import { jumpQuestion } from './jumpQuestion';
import { stopRecord } from './stopRecord';
import { setTotalStar } from './setTotalStar';
import { showTopThree } from './showTopThree';
import { getVolume } from './getVolume';
import {getsurveyData} from "../callNativeFunction/getsurveyData";


dsBridge.register('enableAnswer', enableAnswer);

dsBridge.register('disableAnswer', disableAnswer);

dsBridge.register('jumpQuestion', jumpQuestion);

dsBridge.register('stopRecord', stopRecord);

dsBridge.register('setTotalStar', setTotalStar);

dsBridge.register('showTopThree', showTopThree);

dsBridge.register('getVolume', getVolume);


window.enableAnswer = enableAnswer;
window.getsurveyData = getsurveyData;
window.disableAnswer = disableAnswer;
window.jumpQuestion = jumpQuestion;
window.stopRecord = stopRecord;
window.setTotalStar = setTotalStar;
window.showTopThree = showTopThree;
window.getVolume = getVolume;



