import { uploadScore } from '../callNativeFunction/uploadScore';
import { starCollected } from '../../animations/starCollected';
import { optionScale } from '../../animations/optionScale';
import { Phaser } from '../globalVar';
import { getScoreStar } from '../../animations/getScoreStar';
import { rect } from '../../utils/rect';
import { message } from '../../utils/message';
import { game } from '../globalVar';
import { hideLock } from '../../animations/hideLock';
import { playvoice } from './playvoiceA';

let multiselect = function (game, item, tid) {

    hideLock(game, item['lockGroup']);
    hideLock(game, item['maskGroup']);
    setTimeout(() => {
        if (item['backgroundGroup']) {
            hideLock(game, item['backgroundGroup'], 100);
        }

    }, 2000);

    if (item['optionArr']) {
        for (let i = 0; i < item['optionArr'].length; i++) {
            item['optionArr'][i]['myz'] = 9;
            let temTw = game.add.tween(item['optionArr'][i]);
            item['optionArr'][i].scale.setTo(1);
            temTw.to({
                alpha: 1
            }, 300, Phaser.Easing.Linear.None, true);

            optionScale(game, item['optionArr'][i], 300, 0.2);
        }
    }

    if (item['userChoosedBorder']) {

        for (let i = 0; i < item['userChoosedBorder'].length; i++) {
            item['userChoosedBorder'][i].kill();
        }
    }



    if (item['rotationTween']) {
        for (let i = 0; i < item['rotationTween'].length; i++) {
            item['rotationTween'][i].pause();
            item['optionArr'] && item['optionArr'][i] && item['optionArr'][i].scale.setTo(1);
        }
    }

    if (item['userAnswer']) {

        for (let i = 0; i < item['userAnswer'].length; i++) {
            let temAnswer = item['userAnswer'][i];
            let temIndex = item['data']['answer'].indexOf(temAnswer);
            if (temIndex > -1) {
                let answenItem = item['optionArr'][temAnswer];
                if (answenItem) {
                    // let tick = game.add.image(answenItem.x, answenItem.y, 'choice_correct_tab');
                    answenItem.scale.setTo(1);
                    let tick = rect(game, answenItem.x - answenItem['width'] / 2, answenItem.y - answenItem['height'] / 2, answenItem, 0x3CCC79);
                    tick.anchor.setTo(0.5);

                    optionScale(game, tick, 300, 0.2, item['optionArr'], 2);

                    let tickB = game.add.image(answenItem.x, answenItem.y, 'choice_tick');
                    tickB.anchor.setTo(0.5);

                    optionScale(game, tickB, 300, 0.2, item['optionArr'], 2);


                }
            } else {
                //选错
                let userItem = item['optionArr'][temAnswer];
                if (userItem) {
                    // let choice_error_tab = game.add.image(userItem.x, userItem.y, 'choice_error_tab');
                    userItem.scale.setTo(1);
                    let choice_error_tab = rect(game, userItem.x - userItem['width'] / 2, userItem.y - userItem['height'] / 2, userItem, 0xFF5320);
                    choice_error_tab.anchor.setTo(0.5);

                    optionScale(game, choice_error_tab, 300, 0.2, item['optionArr'], 2);
                }
            }
        }

    }

    /**
     * 漏选
     */

    for (let i = 0; i < item['data']['answer'].length; i++) {
        let temAnswer = item['data']['answer'][i];
        let temIndex = item['userAnswer'].indexOf(temAnswer);
        if (temIndex === -1) {
            let answenItem = item['optionArr'][temAnswer];
            if (answenItem) {
                let tick = game.add.image(answenItem.x, answenItem.y, 'choice_tick');
                tick.anchor.setTo(0.5);

                optionScale(game, tick, 300, 0.2, item['optionArr'], 2);
            }
        }
    }


    let luoFlag = true;

    if (item['data']['answer'].length === item['userAnswer'].length) {
        //全部答对
        let flag = false;
        for (let i = 0; i < item['data']['answer'].length; i++) {
            let temIn = item['data']['answer'].indexOf(item['userAnswer'][i]);
            if (temIn == -1) {
                flag = true;
                break;
            }
        }
        if (flag) {
            //答错
        } else {
            //答对
            luoFlag = false;
            window['GAME_TOTAL_STAR'] += 3;
            starCollected(window['GAME_TOTAL_STAR']);

            uploadScore({ "num": 3, "tid": tid, action: 1 });
            playvoice(game, true);
            message('多选题选对')
            //选对
            getScoreStar(3);
            console.log('答对。。。');
        }
    } else {
        //答错
    }


    if (item['userAnswer'].length === 0) {
        console.log('没打1')

        uploadScore({ "num": 0, "tid": tid, action: 0 });
        playvoice(game, false);
        message('多选题没答题')
        // let GameOverAudio = game.add.audio('GameOverAudio');
        // game.sound.stopAll();
        // GameOverAudio.play();
    } else {
        if (luoFlag) {
            uploadScore({ "num": 0, "tid": tid, action: 1 });
            playvoice(game, false);
            console.log('答错。。。');
            message('多选题答错。。。')

        }

    }



}

export {
    multiselect
}


