import { game } from '../state/game';
import { showMask } from '../utils/showMask';
import { Phaser } from '../base/globalVar';
import { readCss3d } from '../animations/readCss3d';
import { data } from '../base/globalVar';
import { drawText } from '../utils/drawText';

const read = {
    init(data) {
        this.item = data;

    },
    preload() {

    },
    create() {
        showMask(true);
        let item = this.item;
        this.group = game.add.group();
        // 
        drawText(game, item.data.text, 660);



        if (item['data']['css_images'] && item['data']['css_images'].length >= 2) {
            item['divDom'] = readCss3d(item['data']['css_images']);
        }
        /**
         * css3   3d动画的图片
         */



        /**
         * 跟读题动画
         */
        this.readGroup = game.add.group();
        this.barGroup = game.add.group();

        this.readGroup.myz = 100;
        this.barGroup.myz = 102;
        this.group.add(this.readGroup);
        this.group.add(this.barGroup);

        /**
         * 麦克风
         */

        let microphone = game.add.image(554 + 168 / 2, 198 + 263 / 2, 'read_microphone');

        /**
         * 嘴巴
         */

        let mouth = game.add.sprite(598 + 84 / 2, 268 + 64 / 2, 'read_mouth', 0);
        window.mouth = mouth;

        let mouthLowAni = mouth.animations.add('run', [0, 7]);

        let mouthHightAni = mouth.animations.add('run', [1, 2, 3, 4]);
        console.log(mouthHightAni)

        /**
         * 眼睛
         */

        let eyes = game.add.sprite(590 + 100 / 2, 250 + 58 / 2, 'read_eyes', 0);

        /**
         * 闪烁
         */

        let twinkle = game.add.sprite(627 + 26 / 2, 353 + 26 / 2, 'read_twinkle', 0);

        let twinkleAni = twinkle.animations.add('run', [0, 1]);

        /**
         * 添加到组
         */

        this.readGroup.add(microphone);

        this.readGroup.add(mouth);

        this.readGroup.add(eyes);

        this.readGroup.add(twinkle);


        this.readGroup.setAll('anchor.x', 0.5);
        this.readGroup.setAll('anchor.y', 0.5);

        this.readGroup.pivot.x = 554 + 168 / 2;
        this.readGroup.pivot.y = 312 + 263 / 2;

        // this.readGroup.angle = -30;

        this.readGroup.position.x = 554 + 168 / 2;
        this.readGroup.position.y = 312 + 100 + + 263 / 2;

        if (data && data['page_size']) {
            let size = data['page_size'];
            if (size.w === 1024) {
                this.readGroup.position.x = 554 - 128 + 168 / 2;
            }

        }



        item['microphone'] = microphone;

        item['mouth'] = mouth;
        item['mouthLowAni'] = mouthLowAni;
        item['mouthHightAni'] = mouthHightAni;

        item['eyes'] = eyes;

        item['twinkle'] = twinkle;

        item['twinkleAni'] = twinkleAni;
        item['readGroup'] = this.readGroup;
        item['maxVolume'] = game.add.audio('max_volume');


        /**
         * 音量条----和爆炸星星
         */

        item['volumeArr'] = [];

        let read_bar_bg = game.add.image(513 + 220 / 2, 462 + 48 / 2, 'read_bar_bg');
        read_bar_bg.anchor.setTo(0.5);
        read_bar_bg.myz = 10;

        let read_voice1 = game.add.image(525, 472, 'read_voice1');
        read_voice1['alpha'] = 0;
        read_voice1.myz = 15;
        this.barGroup.add(read_voice1);
        item['volumeArr'].push(read_voice1);

        for (let i = 1; i <= 8; i++) {
            let read_voice2 = game.add.image(525 + i * (16 + 4), 472, 'read_voice2');

            read_voice2.myz = 15;
            this.barGroup.add(read_voice2);
            read_voice2['alpha'] = 0;
            item['volumeArr'].push(read_voice2);

        }

        let read_voice3 = game.add.image(525 + 9 * (16 + 4), 472, 'read_voice3');
        read_voice3.myz = 15;
        read_voice3['alpha'] = 0;
        item['volumeArr'].push(read_voice3);
        this.barGroup.add(read_voice3);


        let read_star_big = game.add.image(705 + 64 / 2, 454 + 64 / 2, 'read_star_big_black');
        read_star_big.anchor.setTo(0.5);

        read_star_big.myz = 20;

        this.barGroup.add(read_bar_bg);
        this.barGroup.add(read_star_big);

        this.readGroup.setAll('alpha', 0);

        this.barGroup.setAll('alpha', 0);


        this.barGroup.pivot.x = 513 + 220 / 2;
        this.barGroup.pivot.y = 462 + 48 / 2;



        this.barGroup.position.x = 513 + 220 / 2;

        this.barGroup.position.y = 462 + 20 + 48 / 2;

        if (data && data['page_size']) {
            let size = data['page_size'];
            if (size.w === 1024) {
                this.barGroup.position.x = 513 - 128 + 220 / 2;
            }

        }

        item['read_bar_bg'] = read_bar_bg;
        item['read_star_big'] = read_star_big;

        item['barGroup'] = this.barGroup;










        if (item && item['data'] && item['data']['title']) {
            let title = item['data']['title'];

            for (let i = 0; i < title.length; i++) {
                let img = game.add.sprite(title[i]['pos']['x'], title[i]['pos']['y'], item['id'] + 'title' + i);

                this.title = img;

                img.myz = 100;
                this.group.add(img);

            }

        }

        /**
         * 其他图片other_images
         */
        if (item && item['data'] && item['data']['other_images']) {
            let other_images = item['data']['other_images'];

            for (let i = 0; i < other_images.length; i++) {
                let img = game.add.sprite(other_images[i]['pos']['x'], other_images[i]['pos']['y'], item['id'] + 'other_images' + i);
                img.myz = 8;
                this.group.add(img);
            }
        }


        /**
         * 图片body
         */
        if (item && item['data'] && item['data']['body']) {
            let body = item['data']['body'];

            for (let i = 0; i < body.length; i++) {
                let img = game.add.sprite(body[i]['pos']['x'], body[i]['pos']['y'], item['id'] + 'body' + i);
                img.myz = 8;
                this.group.add(img);
            }
        }


    },
    render() {
        this.barGroup.sort('myz', Phaser.Group.SORT_ASCENDING);
        this.group.sort('myz', Phaser.Group.SORT_ASCENDING);
    },
    update() {


    }
};


export { read };


