

function drawText(game, text, height = 79, strokeColor = '#000000', fillColor = '#ffffff') {
    text = (text || '').trim();
    // console.log(text.length)
    let tempText = game.add.text(game.world.centerX, height, text);

    //	Center align
    tempText.anchor.set(0.5);
    tempText.align = 'center';


    //	Font style
    tempText.font = 'ArialRoundedMTBold';

    tempText.fontSize = 50;
    tempText.setShadow(3, 3, 'rgba(0,0,0,1)', 1);

    tempText.fontWeight = 'bold';

    //	Stroke color and thickness
    tempText.stroke = strokeColor;
    tempText.strokeThickness = 8;
    tempText.fill = fillColor;
    // console.log(tempText.width)

    return tempText;
}

export {
    drawText
}


