
import { message } from './message';
let scoreToStar = function (score) {

    message("-->跟读题得分:" + score);

    score = parseInt(score, 10);

    let star = 0;
    if (score > 0 && score <= 59) {
        star = 1;
    } else if (score >= 60 && score <= 79) {
        star = 2;
    } else if (score >= 80 && score <= 100) {
        star = 3;
    }

    return star;
}

export {
    scoreToStar
};


