import { Phaser } from '../base/globalVar';
function optionScale(game, sprite, time, scale, optionArr, killTime) {

    // let tickTw = game.add.tween(sprite['scale']);
    // tickTw.from({
    //     x: scale,
    //     y: scale,
    // }, time, Phaser.Easing.Linear.None, true);
    if (optionArr) {

        // tickTw.onComplete.add((a, b) => {
        let timer = setTimeout(() => {

            optionArr.map((val) => {

                let temT = game.add.tween(val.scale);
                temT.to({
                    x: 1.1,
                    y: 1.1
                }, 60, Phaser.Easing.Linear.None, true).onComplete.add(function () {
                    temT.to({
                        x: 0,
                        y: 0
                    }, 100, Phaser.Easing.Linear.None, true);
                    temT.onComplete.add(() => {
                        val.kill();
                    });
                });
                return val;

            });

            sprite.kill();
            // console.log('kill dadui')
        }, killTime * 1000);

        window['global_temp_settimeout_timer'].push(timer);

        // });
    }

    // return tickTw;
}

export {
    optionScale
}


