
import { Phaser } from '../base/globalVar';

function lockGroup(game, arr, zIndex, key) {
    //key --->choice_lock
    let gp = game.add.group();
    gp.myz = zIndex;
    for (let i = 0; i < arr.length; i++) {
        let px = arr[i]['pos']['x'] + arr[i]['size']['w'] / 2;
        let py = arr[i]['pos']['y'] + arr[i]['size']['h'] / 2;
        if (key === 'choice_lock') {

            px = arr[i]['pos']['x'] + 16;
            py = arr[i]['pos']['y'] + 16;
        }

        let temp = game.add.sprite(px, py, key);
        if (key !== 'choice_lock') {
            temp.anchor.setTo(0.5);

        }

        if (key !== 'choice_lock') {
            temp.width = arr[i]['size']['w'];
            temp.height = arr[i]['size']['h'];
        }

        let tw = game.add.tween(temp);
        tw.from({
            alpha: 0
        }, 300, Phaser.Easing.Linear.None, true);
        gp.add(temp);

    }
    return gp;

}


export {
    lockGroup
}




