const envValue = process.env.NODE_ENV;
const myconfig = require('../../package.json');
const qurl = require('url');

const soundArr = [
    {
        name: 'error',
        url: '/audio/error.mp3'
    },
    {
        name: 'finish',
        url: '/audio/finish.mp3'
    },
    {
        name: 'pla',
        url: '/audio/pla.mp3'
    },
    {
        name: 'Sound_Amazing',
        url: '/audio/Sound_Amazing.mp3'
    },
    {
        name: 'Sound_Awesome',
        url: '/audio/Sound_Awesome.mp3'
    },
    {
        name: 'Sound_Fantastic',
        url: '/audio/Sound_Fantastic.mp3'
    },
    {
        name: 'Sound_Great',
        url: '/audio/Sound_Great.mp3'
    },
    {
        name: 'Sound_Super',
        url: '/audio/Sound_Super.mp3'
    },
    {
        name: 'Sound_Wonderful',
        url: '/audio/Sound_Wonderful.mp3'
    },
    {
        name: 'Sound_Wow_stupendous',
        url: '/audio/Sound_Wow_stupendous.mp3'
    },
    {
        name: 'Sound_Wrong2',
        url: '/audio/Sound_Wrong2.mp3'
    },
    {
        name: 'Star_Collected',
        url: '/audio/Star_Collected.mp3'
    },
    {
        name: 'StarAdded',
        url: '/audio/StarAdded.mp3'
    },
    {
        name: 'max_volume',
        url: '/audio/max_volume.mp3'
    },
    {
        name: 'StarGet',
        url: '/audio/StarGet.mp3'
    },
    {
        name: 'ClickAudio',
        url: '/audio/Click.mp3'
    },
    {
        name: 'GameOverAudio',
        url: '/audio/GameOver.mp3'
    }
];

if (envValue !== 'development') {
    soundArr.map((item, index) => {
        item['url'] = qurl.parse(myconfig['homepage'])['path'] + item['url'];
        return item;
    });
}


function totalAssets(courseware, game) {
    let len = courseware.length;
    let assetsNum = 1;
    /**
     * 加载爆炸用的星星
     */

    if (game) {
        // game.load.spritesheet('balls', './image/game/css_star.png', 30, 30, 5);
    }

    /**
     * 加载声音
     */
    for (let i = 0; i < soundArr.length; i++) {
        assetsNum += 1;
        if (game) {
            game.load.audio(soundArr[i]['name'], soundArr[i]['url']);
        }
    }

    /**
     * 单选题的公共资源
     */
    if (game) {

        game.load.image('choice_lock', require('../public-image/survey/lock.png'));
        game.load.image('choice_mask', require('../public-image/choice/mask.png'));
        game.load.image('choice_tick', require('../public-image/choice/tick.png'));
        game.load.image('choice_clock', require('../public-image/choice/circle.png'));
        game.load.image('choice_clock_pointer', require('../public-image/choice/pointer.png'));
        game.load.image('choiceWrong', require('../public-image/choice/choiceWrong.png'));
        game.load.image('choiceRight', require('../public-image/choice/choiceRight.png'));
        game.load.image('choiceNoAnswer', require('../public-image/choice/choiceNoAnswer.png'));
    }
    assetsNum += 5;
    /**
     * 问卷题的公共资源
     */
    if (game) {
        game.load.image('survey_clock', require('../public-image/survey/lock.png'));
        game.load.image('survey_score', require('../public-image/survey/picscore.png'));
        game.load.image('survey_bar1', require('../public-image/survey/statistics1.png'));
        game.load.image('survey_bar2', require('../public-image/survey/statistics2.png'));
    }
    assetsNum += 4;


    /**
     * 跟读题的公共资源
     */
    if (game) {
        game.load.image('read_microphone', require('../public-image/read/microphone.png'));

        game.load.spritesheet('read_mouth', require('../public-image/read/mouth.png'), 84, 64, 8);
        game.load.spritesheet('read_eyes', require('../public-image/read/eyes.png'), 100, 58, 4);
        game.load.spritesheet('read_twinkle', require('../public-image/read/twinkle.png'), 26, 26, 2);

        game.load.image('read_star_big', require('../public-image/read/star1.png'));
        game.load.image('read_star_big_black', require('../public-image/read/Shape.png'));
        game.load.image('read_star_small', require('../public-image/read/star2.png'));

        game.load.image('read_bar_bg', require('../public-image/read/bar.png'));

        game.load.image('read_voice1', require('../public-image/read/voice1.png'));

        game.load.image('read_voice2', require('../public-image/read/voice2.png'));

        game.load.image('read_voice3', require('../public-image/read/voice3.png'));





    }
    assetsNum += 11;

    /**
     * raildraw题目
     */
    if (game) {

        game.load.image('raildraw_frog', require('../public-image/raildraw/tracing-frog.png'));
    }





    /**
     * 加载课件图片
     */

    for (let i = 0; i < len; i++) {
        let item = courseware[i];


        loadImages(game, item, 'title');


        loadImages(game, item, 'body');

        /**
         * other_images 图片
         */
        // if (item && item['data'] && item['data']['other_images']) {
        //     let body = item['data']['other_images'];
        //     for (let j = 0; j < body.length; j++) {
        //         assetsNum += 1;
        //         if (game) {
        //             game.load.image(item['id'] + 'other_images' + j, body[j]['image']);
        //         }

        //     }
        // }
        loadImages(game, item, 'other_images');

        /**
         *   ---->groupA图片
         */
        // if (item && item['data'] && item['data']['groupA']) {
        //     let body = item['data']['groupA'];
        //     for (let j = 0; j < body.length; j++) {
        //         assetsNum += 1;
        //         if (game) {
        //             game.load.image(item['id'] + 'groupA' + j, body[j]['image']);
        //         }

        //     }
        // }

        loadImages(game, item, 'groupA');


        /**
         *   ---->groupB图片
         */
        // if (item && item['data'] && item['data']['groupB']) {
        //     let body = item['data']['groupB'];
        //     for (let j = 0; j < body.length; j++) {
        //         assetsNum += 1;
        //         if (game) {
        //             game.load.image(item['id'] + 'groupB' + j, body[j]['image']);
        //         }

        //     }
        // }

        loadImages(game, item, 'groupB');

        /**
        * playvoice
        */
        if (item && item['data'] && item['data']['playVoiceArr']) {
            let body = item['data']['playVoiceArr'];
            for (let j = 0; j < body.length; j++) {
                assetsNum += 1;
                if (game) {

                    game.load.audio(item['id'] + 'playVoiceArr' + j, body[j]['name']);

                }

            }
        }




    }
    return assetsNum;

}

export { totalAssets };


function loadImages(game, item, key) {
    if (item && item['data'] && item['data'][key]) {
        let body = item['data'][key];
        for (let j = 0; j < body.length; j++) {
            let url = (body[j]['image'] || '').trim();
            if (url !== '') {
                if (game) {
                    game.load.image(item['id'] + key + j, url);

                }
            }
        }
    }
}


