import { game } from '../state/game';
import { showMask } from '../utils/showMask';

import { Phaser } from '../base/globalVar';
import { lockGroup } from '../utils/lockGroup';
import { rect, rectBackground } from '../utils/rect';
import { drawText } from '../utils/drawText';

const choice = {
    init(data) {
        this.item = data;


    },
    preload() {

    },
    create() {
        showMask(true);
        console.log('dx')

        let item = this.item;
        item['isAnswered'] = false;
        game.sound.context.resume();

        drawText(game, item.data.text, 660);

        /**
         * 标题图片title
         */
        this.group = game.add.group();
        item.lockGroup = lockGroup(game, item['data']['body'], 11, 'choice_lock');
        // item.maskGroup = lockGroup(game, item['data']['body'], 10, 'choice_mask');

        item.optionArr = [];
        item.stateGroup = this.group;

        this.group.add(item.lockGroup);
        // this.group.add(item.maskGroup);

        if (item && item['data'] && item['data']['title']) {
            let title = item['data']['title'];

            for (let i = 0; i < title.length; i++) {
                let img = game.add.sprite(title[i]['pos']['x'], title[i]['pos']['y'], item['id'] + 'title' + i);

                this.title = img;

                img.myz = 100;
                this.group.add(img);

            }

        }

        /**
         * 其他图片other_images
         */
        if (item && item['data'] && item['data']['other_images']) {
            let other_images = item['data']['other_images'];

            for (let i = 0; i < other_images.length; i++) {
                let img = game.add.sprite(other_images[i]['pos']['x'], other_images[i]['pos']['y'], item['id'] + 'other_images' + i);
                img.myz = 8;
                this.group.add(img);
            }
        }

        /**
         * 答题结束的动画图片
         */
        if (item && item['data'] && item['data'].hasOwnProperty('playVoiceArr') && item['data']['playVoiceArr'].length) {
            item.overAnswerimggroup = [];

            let choiceNoAnswer = game.add.image(693, 352, 'choiceNoAnswer');
            let choiceRight = game.add.image(693, 352, 'choiceRight');
            let choiceWrong = game.add.image(693, 352, 'choiceWrong');

            choiceNoAnswer.myz = 14;
            choiceRight.myz = 14;
            choiceWrong.myz = 14;

            this.group.add(choiceNoAnswer);
            this.group.add(choiceRight);
            this.group.add(choiceWrong);

            choiceNoAnswer.anchor.setTo(0.5);
            choiceRight.anchor.setTo(0.5);
            choiceWrong.anchor.setTo(0.5);

            choiceNoAnswer.scale.setTo(0.5);
            choiceRight.scale.setTo(0.5);
            choiceWrong.scale.setTo(0.5);

            choiceNoAnswer.alpha = 0;
            choiceRight.alpha = 0;
            choiceWrong.alpha = 0;

            item.overAnswerimggroup.push(choiceNoAnswer);
            item.overAnswerimggroup.push(choiceRight);
            item.overAnswerimggroup.push(choiceWrong);
        }

        /**
         * 可以点击选择的图片body
         */

        if (item && item['data'] && item['data']['body']) {
            let body = item['data']['body'];

            item['userAnswer'] = undefined;
            let imgGroup = [];
            let borderGroup = [];
            let ClickAudio = game.add.audio('ClickAudio');
            item.maskGroup = game.add.group();
            item.maskGroup.myz = 10;
            this.group.add(item.maskGroup);


            //backgroundGroup
            item.backgroundGroup = game.add.group();
            item.backgroundGroup.myz = 1;
            this.group.add(item.backgroundGroup);


            for (let i = 0; i < body.length; i++) {
                let img = game.add.sprite(body[i]['pos']['x'] + body[i]['size']['w'] / 2, body[i]['pos']['y'] + body[i]['size']['h'] / 2, item['id'] + 'body' + i);
                img.anchor.setTo(0.5);
                item.optionArr.push(img);

                img['answer'] = i;

                //蒙版效果
                img.alpha = 0.5;

                img.myz = 9;

                let imgtw = game.add.tween(img);
                imgtw.from({
                    alpha: 0
                }, 300, Phaser.Easing.Linear.None, true);

                imgGroup.push(img);

                this.group.add(img);
                let that = this;
                // let temMask = rect(game, img['position']['x'] - img['width'] / 2, img['position']['y'] - img['height'] / 2, img, 0x00AEFC, true);

                // item.maskGroup.add(temMask);


                //backgroundGroup
                let temBack1 = rectBackground(game, img['position']['x'] - img['width'] / 2, img['position']['y'] - img['height'] / 2, img['width'], img['height'], 0x000000, 45, 0.3);
                let temBack2 = rectBackground(game, img['position']['x'] - img['width'] / 2, img['position']['y'] - img['height'] / 2, img['width'] - 5, img['height'] - 5, 0xffffff, 40, 1);

                item.backgroundGroup.add(temBack1);
                item.backgroundGroup.add(temBack2);


                img.inputEnabled = true;
                img.events.onInputDown.add(function () {
                    game.sound.stopAll();
                    ClickAudio.play();
                    item['isAnswered'] = true;
                    for (let i = 0; i < imgGroup.length; i++) {
                        imgGroup[i]['myz'] = 9;
                        if (imgGroup[i] !== img) {
                            let temTw = game.add.tween(imgGroup[i]);
                            temTw.to({ alpha: 0.5 }, 300, Phaser.Easing.Linear.None, true);
                        }
                    }
                    img.alpha = 1;

                    for (let i = 0; i < borderGroup.length; i++) {
                        borderGroup[i].kill();
                    }

                    img.myz = 11;

                    img.scale.setTo(1);

                    let border = rect(game, img['position']['x'] - img['width'] / 2, img['position']['y'] - img['height'] / 2, img, 0x00AEFC);
                    border.myz = 12;
                    that.group.add(border);

                    let tw = game.add.tween(img.scale);


                    tw.from({ x: 1.1, y: 1.1 }, 300, Phaser.Easing.Linear.None, true);


                    // let border = game.add.sprite(img.position.x, img.position.y, 'choice_highligh');
                    borderGroup.push(border);

                    let heightlight = game.add.tween(border);
                    heightlight.to({
                        alpha: 0.6
                    }, 300, Phaser.Easing.Linear.None, true, 0, Infinity, true);
                    console.log(border)

                    item['userAnswer'] = this.answer;
                    item['userChoosedBorder'] = border;

                }, img)

            }
        }

    },

    render() {
        this.group.sort('myz', Phaser.Group.SORT_ASCENDING);
    }
};


export { choice };


