

let readCss3d = function (imgArr) {

    console.log(imgArr);
    let div = document.createElement('div');

    div.className = 'read_3d_box';
    let body = document.body;
    div.innerHTML = '<div class="content"><div class="read_c1"><img src="' + imgArr[0]['image'] + '" alt=""></div><div class="read_c2"><img src="' + imgArr[1]['image'] + '" alt=""></div></div>'

    body.appendChild(div);
    return div;
}

let readRemoveChild = function (dom, time) {
    let div = dom.querySelector('.content');
    if (div) {

        div.className += '  content_active';
        setTimeout(() => {
            if (dom && document.body.contains(dom)) {
                document.body.removeChild(dom);

            }
        }, time * 1000);
    }
}

let removeAll3dBox = function () {
    let doms = document.querySelectorAll('.read_3d_box');
    let len = doms.length;
    for (let i = 0; i < len; i++) {
        if (doms[i]) {
            let f = document.body.contains(doms[i]);
            if (f) {
                document.body.removeChild(doms[i]);
            }
        }
    }
}

export {
    readCss3d,
    readRemoveChild,
    removeAll3dBox
};


