// import { uploadScore } from '../callNativeFunction/uploadScore';
// import { starCollected } from '../../animations/starCollected';
import { message } from '../../utils/message';
import { Phaser } from '../globalVar';
import { readRemoveChild } from '../../animations/readCss3d';


// import { getScoreStar } from '../../animations/getScoreStar';

let read = function (game, item, tid) {

    //选对

    message('跟读题停止答题');

    item['twinkleAni'] && item['twinkleAni'].stop();
    item['mouthHightAni'] && item['mouthHightAni'].stop();
    item['mouthLowAni'] && item['mouthLowAni'].stop();

    if (item['divDom']) {
        readRemoveChild(item['divDom'], 5);
    }

    // let tw = game.add.tween(item['readGroup']);
    // tw.to({
    //     angle: 0
    // }, 100, Phaser.Easing.Linear.None, true);

    setTimeout(() => {
        //消失
        if (item['readGroup']) {

            for (let i = 0; i < item['readGroup']['children'].length; i++) {
                let tw = game.add.tween(item['readGroup']['children'][i]);
                tw.to({
                    alpha: 0
                }, 200, Phaser.Easing.Linear.None, true);
            }
        }

        if (item['barGroup']) {

            for (let i = 0; i < item['barGroup']['children'].length; i++) {
                let tw = game.add.tween(item['barGroup']['children'][i]);
                tw.to({
                    alpha: 0
                }, 200, Phaser.Easing.Linear.None, true);
            }
        }

        if (item['readGroup']) {

            let myss = game.add.tween(item['readGroup']['scale']);

            myss.to({
                x: 0.8,
                y: 0.8
            }, 200, Phaser.Easing.Linear.None, true);
        }



        //
        if (item['barGroup']) {

            let bfff = game.add.tween(item['barGroup']['scale']);
            bfff.to({
                x: 0.8,
                y: 0.8
            }, 200, Phaser.Easing.Linear.None, true);
        }


    }, 2000)







}

export {
    read
}


